<template>
  <div class="header">
    <div class="top-header fixed-top border-bottom">
      <div class="top-back">
        <a @click="Back"></a>
        <!--<a href="javascript:history.go(-1)"></a>-->
      </div>
      <h2 class="f36" v-text="headerText"></h2>
      <div class="top-right"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Header',
    data () {
      return {
        headerText: '',
      }
    },
    mounted(){
      var self = this;
      self.$bus.on('headerText', title => {
        self.headerText = title;
      })
    },
    methods: {
      Back(){
        this.$router.go(-1);
        //window.history.back()
      }
    }
  }
</script>


<style scoped>
  .top-header {
    width: 100%;
    max-width: 768px;
    margin: 0 auto;
    position: relative;
    background-color: #f6f6f6;
    line-height: .96rem;
    z-index: 50;
  }

  .fixed-top {
    position: fixed !important;
    top: 0;
    max-width: 768px;
    width: 100%;
  }

  .border-bottom {
    border-bottom: 1px solid #e8e8e8;
  }

  .top-header .top-back, .top-header .top-home {
    position: absolute;
    left: 0;
    min-width: 10%;
    height: .96rem;
  }

  top-header .top-back a, .top-header .top-home a {
    display: inline-block;
    min-width: .55rem;
    width: 100%;
    height: .96rem;
    text-indent: -9999px;
  }

  .top-header .top-back a {
    display: inline-block;
    min-width: .55rem;
    width: 100%;
    height: .96rem;
    text-indent: -9999px;
    background: url(../assets/images/common/left.png) .3rem no-repeat;
    background-size: .21rem;
  }

  .top-header h2 {
    max-width: 80%;
    margin-left: auto;
    margin-right: auto;
    height: .96rem;
    font-weight: 400;
    line-height: .96rem;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
  }

  .f36 {
    font-size: .36rem !important;
  }

  .top-header .top-right {
    position: absolute;
    top: 0;
    right: 0;
    min-width: 10%;
    font-size: 0;
    height: .96rem;
    line-height: .96rem;
    -webkit-text-size-adjust: none;
    text-align: right;
  }
</style>



